  import {pathToBase64} from '../../utils/base64.js'
  import urls from '../../utils/urls.js'
  export default {
    data () {
      return { // 定义变量
        placeholder: '请您详细描述问题发生的场景和现象,并添加截图,这样有助于我们快速解决您的问题',
        imgArr: [],
        per: 1,
        intro: '',
        phone: '',
        errInput: false,
        errTextarea: false,
        isSure: false,
				isLogin: this.$store.state.isLogin
      }
    },
    methods: {
      introCheck: function () {
        if (!this.intro) {
          this.errTextarea = true
        } else {
          this.errTextarea = false
        }
      },
      phoneCheck: function () {
        if (!this.phone) {
          this.errInput = true
        } else {
          this.errInput = false
        }
      },
      addImg: function () {
        // var id = uni.getStorageSync('user').id
				// let id = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        uni.chooseImage({
        	count: 3,
          sourceType: ['album'],
          success:(res) => {
            // console.log(res.tempFilePaths)
            this.imgArr = res.tempFilePaths
            let arr = []
            for (let i = 0; i < res.tempFilePaths.length; i++) {
              pathToBase64(res.tempFilePaths[i])
              .then(base64 => {
                arr[i] = base64
              })
            }
            setTimeout(() => {
              uni.request({
                url: urls.HTTP + '/user/upload_multi',
                method: 'POST',
                data: {
                  images: arr
                },
                success: (res) => {
                  // console.log('上传图片', res)
                  this.per = 100
                  this.imgArr = res.data.path
                }
              })
            }, 500)
            let interval = setInterval(() => {
              if (this.per < 61) {
                this.per = this.per + 30
              } else if (this.per >= 61 && this.per < 100) {
                this.per = 94
                clearInterval(interval)
              }
            }, 500)
          }
        })
      },
      sure: function (e) {
        if (!this.intro) {
          this.errTextarea = true
        } else if (!this.phone) {
          this.errInput = true
        } else {
          this.errInput = false
          this.isSure = true
          // let id = uni.getStorageSync('user').id
					// let id = JSON.parse(uni.getStorageSync('userInfo')).id
					let id = '';
					if(this.isLogin){
						id = JSON.parse(uni.getStorageSync('userInfo')).id
					}else{
						 id = 0
					}
          uni.request({
            url: urls.HTTP + '/user/userfeedback',
            method: 'POST',
            data: {
              picpath: this.imgArr,
              uid: id,
              content: this.intro,
              contact: this.phone
            },
            success: (res) => {
              this.isSure = false
              if (res.data.code === 0) {
                uni.showToast({
                  title: '提交成功'
                })
                this.intro = ''
                this.imgArr = []
                this.phone = ''
              } else {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
              }
            }
          })
        }
      }
    },
    computed: {
      imgurl () {
        if (this.per !== 100) {
          return this.imgArr
        } else {
          let arr = []
          for (let i = 0; i < this.imgArr.length; i++) {
            arr[i] = urls.UrlHttp + this.imgArr[i]
          }
          return arr
        }
      }
    }
  }
